<template>
<div class="resultBox">
  <h4 class="lz-title-h4">核保结论</h4>
   <div class="resultText">
    <div class="question" v-html="intell.results.resultInfo"></div>
    <div class="btn" v-if="intell.results.result == 0 || intell.results.result == 3">
      <div class="lz-isIndex"  @click="resultNext(1)">
        继续投保 
      </div>
    </div>
    <div class="btn" v-else-if="intell.results.result == 1">
      <div class="lz-isIndex"  @click="resultNext(1)">
        继续投保  
      </div>
      <div class="lz-isIndex"  @click="resultNext(0)">
        取消投保  
      </div>
    </div>
    <div class="btn" v-if="intell.results.result == 2">
      <div class="lz-isIndex"  @click="resultNext(0)">
        核保失败，请点击确认 
      </div>
    </div>
    <div class="btn" v-if="intell.results.result == 5">
      <div class="lz-isIndex"  @click="resultNext(1)">
        接受并转人核 
      </div>
      <div class="lz-isIndex"  @click="resultNext(0)">
        取消投保  
      </div>
    </div>
  </div>
  </div>
</div>
</template>
<script>
  import { mapMutations } from "vuex";
  import { mapGetters } from "vuex";
  import _ from "lodash";
  var vm;
  export default {
    name: "Result",
    data() {
      return {
        desLevel:0,
        resultdata:[],
        writeInformDatas:''
      };
    },
    created() {
      vm = this;
      this.intell.indexList.splice(0);
      this.getInfo()
      this.set_msg({key: "intelligent",data: this.intell}); //将当前选择项缓存
    },
    methods: {
      getInfo(){
        this.$http.getHttp({
            name: "getInfo",
            params: [{ name: "key", val: this.intell.int_quey.key }],
            header: 2
          },res => {
            vm.writeInformDatas = JSON.parse(res.data).info;
            console.log(vm.writeInformDatas)
        });
      },
      //0 返回产品页 1继续投保 
      resultNext(type) {
        if (type == 0) {
          window.location.href = location.origin + "/jump.html?url="
          + encodeURIComponent(location.origin+"/market/premium-family.html?id="+this.intell.int_quey.code+"&key="+this.intell.int_quey.key);
        } else if (type == 1) {
          this.writeInformDatas.writeInform.fromvalue.intelligent = this.intell.results;
          this.writeInformDatas.writeInform.fromvalue.intelligent.deseaseInfo = this.dessFilter();
          this.writeInformDatas.writeInform.fromvalue.paperId = this.intell.paperId;
          var load = this.$popup.loading("正在请求数据...");
          this.$http.getHttp({
              name: "setInfo",
              params:{info:this.writeInformDatas} ,
            },data => {
              console.log(data)
               this.$popup.closeLay(load);
              var process = JSON.parse(sessionStorage.getItem("productProcess"));
              if (process.orderPage == 1) {
                window.location.href = location.origin + "/jump.html?url="
                + encodeURIComponent("./market/writeInform-moreinsure.html?key="+data.data+"&proCode=" + this.intell.int_quey.code + "")
              } else if (process.orderPage == 2) {
                window.location.href = location.origin + "/jump.html?url="
                + encodeURIComponent("./market/writeInform-3.html?key=" + data.data+"&proCode=" + this.intell.int_quey.code + "")
              }
          });
        }
      }, 
       dessFilter(){
        var self = this;
        var resultdata;
        _.forEach(this.intell.issList,(v,i) => {
          if(!resultdata){
            resultdata = v.diseaseDesc;
          }else{
            resultdata= resultdata + "," + v.diseaseDesc
          }
        });
        return resultdata
      }, 
      ...mapMutations({
        set_msg: "SET_MESSAGE"
      })
    },
    //传入后台的结果过滤
    components: {},
    computed: {
      // 使用对象展开运算符将 getter 混入 computed 对象中
      ...mapGetters([
        "intell"
      ])
    },
    mounted() {
      
    }
  };
</script>

<style lang="scss">
.resultBox {
  min-height: 100%;
  background-color: #fff;
  font-size: 16px;
  .resultText {
    padding: 15px;
    line-height: 1.5;
    background-color: #fff;
    border-bottom: 1px solid #ccc;
    .question {
      padding-bottom: 20px;
    }
  }
}
</style>


